<template>
  <div>
    <h1>{{this.id ? '' : ''}}</h1>
    <el-form label-width="120px" @submit.native.prevent="save">
      <el-form-item label="">
        <el-select v-model="model.category" multiple>
          <el-option
            v-for="item in categories"
            :key="item._id"
            :label="item.name"
            :value="item._id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="model.title"></el-input>
      </el-form-item>
      <el-form-item label="">
        <vue-editor v-model="model.body" 
        @image-added="handleImageAdded">
        </vue-editor>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import {request} from 'network/request.js'

  // 
  import {getCategories} from 'network/category'
  import {addArticle,updateArticle,getArticle} 
  from 'network/article'

  // 
  import { VueEditor} from "vue2-editor";

  export default {
    props: {
      id: String
    },
    data(){
      return {
        model: {},  // 
        categories: [] // 
      }
    },
    created(){
      // 
      this.id && this.fetch();
      // 
      this.queryCategories();
    },
    methods: {
      // Article
      async save(){
        if(this.id) {
          // IdArticle
          await updateArticle(this.id,this.model);
        }else{
          // Article
          await addArticle(this.model);       
        }
        this.$message({
          message: '',
          type: 'success'
        });
        this.id && this.$router.push('/articles/list')
      },
      // 
      async fetch(){
        this.model = await getArticle(this.id);
      },
      // 
      async queryCategories(){
        this.categories = await getCategories();
      },
      // 
      async handleImageAdded(file, Editor, cursorLocation, resetUploader){
        const formData = new FormData();
        formData.append("file",file);
        const result = await request({
          url: '/upload',
          method: "post",
          data: formData
        });
        console.log(result);
        Editor.insertEmbed(cursorLocation, "image", result.url);
        resetUploader();
      }
    },
    components: {
      vueEditor: VueEditor
    }
  }
</script>

